const html = `<h1 id="constructor-solution">Constructor (Solution)</h1>
<h3 id="solution">Solution</h3>
<pre><code class="language-shell">git checkout exercise-00-solution</code></pre>
<pre><code class="language-solidity">constructor(address[] memory _owners, uint _numConfirmationsRequired) public {
  require(_owners.length &gt; 0, "owners required");
  require(
    _numConfirmationsRequired &gt; 0 &amp;&amp; _numConfirmationsRequired &lt;= _owners.length,
    "invalid number of required confirmations"
  );

  for (uint i = 0; i &lt; _owners.length; i++) {
    address owner = _owners[i];

    require(owner != address(0), "invalid owner");
    require(!isOwner[owner], "owner not unique");

    isOwner[owner] = true;
    owners.push(owner);
  }

  numConfirmationsRequired = _numConfirmationsRequired;
}</code></pre>
`

export default html
