const html = `<h1 id="revoke-confirmation-exercise">Revoke Confirmation (Exercise)</h1>
<h3 id="solution">Solution</h3>
<pre><code class="language-shell">git checkout exercise-05-solution</code></pre>
<pre><code class="language-solidity">function revokeConfirmation(uint _txIndex)
    public
    onlyOwner
    txExists(_txIndex)
    notExecuted(_txIndex)
{
    Transaction storage transaction = transactions[_txIndex];

    require(transaction.isConfirmed[msg.sender], "tx not confirmed");

    transaction.isConfirmed[msg.sender] = false;
    transaction.numConfirmations -= 1;

    emit RevokeConfirmation(msg.sender, _txIndex);
}</code></pre>
`

export default html
