const html = `<h1 id="confirm-transaction-solution">Confirm Transaction (Solution)</h1>
<h3 id="solution">Solution</h3>
<pre><code class="language-shell">git checkout exercise-03-solution</code></pre>
<pre><code class="language-solidity">modifier txExists(uint _txIndex) {
    require(_txIndex &lt; transactions.length, "tx does not exist");
    _;
}

modifier notExecuted(uint _txIndex) {
    require(!transactions[_txIndex].executed, "tx already executed");
    _;
}

modifier notConfirmed(uint _txIndex) {
    require(!transactions[_txIndex].isConfirmed[msg.sender], "tx already confirmed");
    _;
}</code></pre>
<pre><code class="language-solidity">function confirmTransaction(uint _txIndex)
    public
    onlyOwner
    txExists(_txIndex)
    notExecuted(_txIndex)
    notConfirmed(_txIndex)
{
    Transaction storage transaction = transactions[_txIndex];

    transaction.isConfirmed[msg.sender] = true;
    transaction.numConfirmations += 1;

    emit ConfirmTransaction(msg.sender, _txIndex);
}</code></pre>
`

export default html
