const html = `<h1 id="execute-transaction-solution">Execute Transaction (Solution)</h1>
<h3 id="solution">Solution</h3>
<pre><code class="language-shell">git checkout exercise-04-solution</code></pre>
<pre><code class="language-solidity">function executeTransaction(uint _txIndex)
    public
    onlyOwner
    txExists(_txIndex)
    notExecuted(_txIndex)
{
    Transaction storage transaction = transactions[_txIndex];

    require(
        transaction.numConfirmations &gt;= numConfirmationsRequired,
        "cannot execute tx"
    );

    transaction.executed = true;

    (bool success, ) = transaction.to.call.value(transaction.value)(transaction.data);
    require(success, "tx failed");

    emit ExecuteTransaction(msg.sender, _txIndex);
}</code></pre>
`

export default html
