const html = `<h1 id="submit-transaction-solution">Submit Transaction (Solution)</h1>
<h3 id="solution">Solution</h3>
<pre><code class="language-shell">git checkout exercise-02-solution</code></pre>
<pre><code class="language-solidity">modifier onlyOwner() {
    require(isOwner[msg.sender], "not owner");
    _;
}</code></pre>
<pre><code class="language-solidity">function submitTransaction(address _to, uint _value, bytes memory _data)
    public
    onlyOwner
{
    uint txIndex = transactions.length;

    transactions.push(Transaction({
        to: _to,
        value: _value,
        data: _data,
        executed: false,
        numConfirmations: 0
    }));

    emit SubmitTransaction(msg.sender, txIndex, _to, _value, _data);
}</code></pre>
`

export default html
